export default {
	data() {
		return {
			searchValue:'',
			exist:true,
			productList:[],
			tabBars: [{
							id:'',
							label:'全部',
						}],
			tabIndex: 0,
			classifyId:'',
			pageNum: 1,
			pageSize: 10,
			ismore:false,
			contentText: {
			    contentdown: '查看更多',
			    contentrefresh: '加载中',
			    contentnomore: '没有更多',
			},
			totalpage: 10000
		};
	},
	onLoad(options) {
		this.getProduct()
	},
	computed: {
		communityId(){
			return this.$store.getters.communityId
		}
	},
	onReachBottom() {
	    this.status = 'loading'
	    
	    this.pageNum++
		if(this.pageNum > this.totalpage) {
			this.status="noMore"
			return false
		}
	    this.getProduct()
	},
	methods: {
		goDetail(id, type){
			uni.navigateTo({
				url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${type}`,
			});
		},
		getProduct(){
			let that = this
			this.$api.communityBestPorductPage({
				communityId: this.communityId,
				keywords:this.searchValue,
				size:this.pageSize,
				page:this.pageNum,
			}).then(res=>{
				if(res.status=="OK"){
					console.log('this.res is ', res)
					this.totalpage = res.totalPages
					if(that.pageNum ==1)
					{
						that.productList = [];
					}
					res.list.map(item=>{
						that.productList.push({
							price:item.price/100,
							originalPrice:item.originalPrice/100,
							name:item.name,
							pic:JSON.parse(item.pics)[0].url,
							id:item.id,
							code:item.code
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		search(res){
			this.pageNum = 1;
			this.searchValue = res.value;
			this.getProduct()
		}
		
	},
	filters: {
		toFixed2: function(value) {
			return value.toFixed(2);
		},
		toFixed1: function(value) {
			return value.toFixed(1);
		},
	}
	
}
